.product-description {
  font-size: 14px;
  margin-bottom: 10px;
  max-height: 70px;
  overflow: hidden;
  position: relative;
  transition: max-height 0.3s ease;
}

.product-description.collapsed {
  max-height: 70px;
}

.product-description.expanded {
  max-height: none;
  overflow: visible;
}

.expand-button {
  bottom: 5px;
  right: 5px;
  padding: 5px 10px;
  color: #007bff;
  cursor: pointer;
  font-size: 12px;
}

@media (max-width: 768px) {
  .product-description {
    max-height: 50px;
  }

  .expand-button {
    bottom: -15px;
    right: 0;
  }
}




