.loading-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 200px;
}

.loading-message {
  font-size: 16px;
  margin-top: 10px;
}

/* Center the spinner horizontally */
.loading-container .spinner-border {
  margin: 0 auto;
}


