.about-container {
    padding: 40px;
    background-color: #f7f7f7;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    min-height: calc(100vh - 120px); /* Adjust the value based on your layout */
    box-sizing: border-box; /* Include padding in the height calculation */
  }
  
  .about-heading {
    font-size: 32px;
    margin-bottom: 20px;
  }
  
  .about-content {
    max-width: 800px;
    margin: 0 auto; /* Center the content horizontally */
  }
  
  .about-image {
    width: 200px;
    height: auto;
    margin-bottom: 20px;
  }
  
  .about-description {
    font-size: 16px;
    margin-bottom: 20px;
  }
  
  @media (max-width: 767px) {
    .about-heading {
      font-size: 28px;
    }
  
    .about-content {
      padding: 0 20px;
    }
  
    .about-description {
      font-size: 14px;
    }
  }
  